const btnNav = document.querySelector(".menu-nav");
const navMenu = document.querySelector(".navigation-wrapper");
const iconShow = document.querySelector("#menuShow");
const iconHide = document.querySelector("#menuClose");

if (btnNav) {
  btnNav.onclick = function () {
    if (navMenu.classList.contains("d-none")) {
      iconShow.classList.add("d-none");
      iconHide.classList.remove("d-none");
      navMenu.classList.remove("d-none");
      navMenu.classList.add("d-block", "animate__animated", "animate__fadeIn");
    } else {
      navMenu.classList.remove(
        "d-block",
        "animate__animated",
        "animate__fadeIn"
      );
      navMenu.classList.add("d-none");
      iconShow.classList.remove("d-none");
      iconHide.classList.add("d-none");
    }
  };
}

