const copyBtn = document.querySelector(".copyBtn");
const copyText = document.querySelector("#copyInput");
const toastLiveExample = document.getElementById("copyToast");
const copyBtn2 = document.querySelector(".copyBtn2");
const copyText2 = document.querySelector("#copyInput2");

copyBtn.onclick = () => {
  copyText.select();
  copyText.setSelectionRange(0, 99999); /* For mobile devices */
  navigator.clipboard.writeText(copyText.value);

  const toast = new bootstrap.Toast(toastLiveExample);
  toast.show();
};
copyBtn2.onclick = () => {
  copyText2.select();
  copyText2.setSelectionRange(0, 99999); /* For mobile devices */
  navigator.clipboard.writeText(copyText2.value);

  const toast = new bootstrap.Toast(toastLiveExample);
  toast.show();
};

const imagePreview = document.querySelector("#imagePreview");
const imageUpload = document.querySelector("#imageUpload");

if (imagePreview || imageUpload) {
  function readURL(input) {
    console.log("the", input);
    if (input.files && input.files[0]) {
      const reader = new FileReader();

      reader.onload = function (e) {
        imagePreview.attr("src", e.target.result);
      };

      reader.readAsDataURL(input.files[0]);
    }
  }

  imageUpload.change(function () {
    readURL(this);
  });
}

// CounterDate
const dateTime = document.querySelector("#dateTime");
const countDownDate = new Date(dateTime.value).getTime();
const x = setInterval(function () {
  const now = new Date().getTime();
  const distance = countDownDate - now;
  const hours = Math.floor(
    (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
  );
  const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
  const seconds = Math.floor((distance % (1000 * 60)) / 1000);
  function makeMeTwoDigits(n) {
    return (n < 10 ? "0" : "") + n;
  }

  document.getElementById("timeCounter").innerHTML =
    makeMeTwoDigits(hours) +
    ":" +
    makeMeTwoDigits(minutes) +
    ":" +
    makeMeTwoDigits(seconds);

  if (distance < 0) {
    clearInterval(x);
    document.getElementById("timeCounter").innerHTML = "EXPIRED";
  }
}, 1000);

